.profile__avatar-edit {
  position: relative;
  padding: 0;
  width: 120px;
  height: 120px;
  border-radius: 50%;
  border: none;
  margin: 0 30px 0 0;
}

.profile__avatar-edit::before {
  content: '';
  background: url(../../../images/edit_avatar.svg) center no-repeat;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .8);
  display: inline-block;
  left: 0;
  top: 0;
  opacity: 0;
  position: absolute;
  transition: opacity .3s linear;
}

.profile__avatar-edit:hover::before {
  opacity: 1;
}
